.acting-rows {
    display: flex;
    flex-direction: column;
    gap: 4em;
    width: 100%;
    justify-content: space-around;
}

.acting-row {
    width: 100%;
}

.gallery {
    display: flex;
    justify-content: space-evenly;
    // flex-direction: column;
    gap: 2em;
}

.video-container {
    flex-basis: 50%;
    box-shadow: rgba(0, 0, 0, 0.746) -4px 4px 10px;
}

.headshot-card {
    position: relative;
    box-shadow: rgba(0, 0, 0, 0.746) -4px 4px 10px;
    transform-style: preserve-3d;
    
    &:hover {
        .back {
            opacity: .8;
        }
    }

    .expand {
        position: absolute;
        cursor: pointer;
        color: $off-white;
        font-size: 1.4rem;
        top: 4px;
        right: 4px;
        z-index: 2000;

        &:hover {
            color: $arg-yellow;
        }

        @keyframes expand {
            0% {
                font-size: 1.4rem;
            }
            50% {
                font-size: 2rem;
            }
            100% {
                font-size: 1.4rem;
            }
        }
    }

    img {
        backface-visibility: hidden;
    }

    .back {
        backface-visibility: hidden;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.616);
        opacity: 0;
        transition: all .4s ease-in-out;

        a {
            color: $off-white;
            text-transform: uppercase; 

            &:hover {
                color: $arg-yellow;
            }
        }
    }
}

@media (max-width: 1280px) {
   
}

@media (max-width: 700px) {

    .acting-rows {
        // padding: 2em 4em;
        // flex-direction: column;
        align-items: center;
    }

    .acting-row {
        width: 100%;
    }

    .gallery {
        flex-direction: column;
    }
}
